import styled, { css } from "styled-components";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { Title2, Text, InnerWrap } from "../../assets/styles/component";

import joystick from "../../assets/images/Joystick.png";
import ecom_item_1 from "../../assets/images/box.svg";
import ecom_item_2 from "../../assets/images/ticket.svg";
import ecom_item_3 from "../../assets/images/joystick.svg";
import violet_arrow from "../../assets/images/violet_arrow.svg";
import yellow_arrow from "../../assets/images/yellow_arrow.svg";

const bg = css`
  content: "";;
  z-index: -100;
  position: absolute;
  overflow: hidden;
`;

const SECommerce = styled.section`
  position: relative;
  padding: 70px 0;

  & > div {
    & * {
      z-index: 5;
    }

    & > p {
      text-align: center;
      margin-bottom: 30px;
    }

    &::after {
      ${bg}
      width: 58vw;
      height: 58vw;
      right: 0%;
      bottom: 0%;

      background: #f10275;
      filter: blur(150px);
      transform: translate(15%, 65%);
      border-radius: 50%;
      opacity: 0.4;

      mix-blend-mode: screen;
    }

    &::before {
      ${bg}
      width: 58vw;
      height: 58vw;
      left: 50%;
      top: 50%;

      opacity: 0.5;
      background: #0202f1;
      filter: blur(400px);

      border-radius: 50%;
      transform: translate(-40%, -30%);

      mix-blend-mode: screen;
    }
  }

  &::after {
    ${bg}
    width: 58vw;
    height: 58vw;
    right: 0%;
    bottom: 0%;

    background: #ff429e;
    filter: blur(150px);
    transform: translate(15%, 65%);
    border-radius: 50%;
    opacity: 0.4;

    mix-blend-mode: screen;
  }
`;

const Title = styled(Title2)`
  margin-bottom: 20px;
  font-size: 26px;
`;

const Joystick = styled(LazyLoadImage)`
  position: absolute;
  width: 140vw;
  min-width: 400px;
  max-width: 500px;
  margin-top: -70px;
  top: 0%;
  left: 50%;
  transform: translate(-50%);

  animation: move 10s ease-in-out infinite;

  @keyframes move {
    0% {
      transform: translate(-50%) rotate(-5deg);
    }

    50% {
      transform: translate(-50%, 1%) rotate(-5deg);
    }

    100% {
      transform: translate(-50%) rotate(-5deg);
    }
  }
`;

const List = styled.div`
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  gap: 50px;
  width: 100%;
  z-index: 5;

  justify-content: center;
  align-items: center;

  grid-template-rows: 222px 222px;
`;

const ListItem = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-self: center;
  text-align: left;
  z-index: 5;

  &:not(:last-child) {
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.19);
    padding: 54px 23px 25px 23px;
    height: fit-content;
    border-radius: 20px;
    max-width: 245px;

    p {
      font-size: 10px;
      line-height: 20px;
    }
  }

  img:first-child {
    left: -10px;
  }

  &:nth-child(3) img:last-child {
    left: 35%;
    top: -30%;
    max-width: 80px;
    transform: rotate(180deg);
  }

  &:nth-child(4) {
    margin-bottom: 30px;

    img:last-child {
      right: 30%;
      bottom: -20%;
      max-width: 80px;
      transform: scale(-1.4, 1.4);
    }
  }
`;

const Promo = styled.div``;

const Button = styled.button`
  display: block;

  margin: 0 auto;
  padding: 20px 60px;

  border: 0;

  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.01em;
  color: #29009c;

  background: #ffffff;
  outline: 10px solid rgba(255, 255, 255, 0.1);
  border-radius: 73px;
  transition: 0.25s ease-in-out;

  cursor: pointer;

  &:hover {
    transform: scale(1.01);
    outline: 14px solid rgba(255, 255, 255, 0.1);
  }
`;

const Img = styled(LazyLoadImage)`
  position: absolute;
  height: 180px;
  top: -90px;
  width: 128px;
`;

const Arrow = styled(LazyLoadImage)`
  position: absolute;
`;

const ECommerce = ({ language }) => {
  return (
    <SECommerce>
      <InnerWrap>
        <Title>
          Эффективно <br /> для e-commerce
        </Title>
        <Text fix_size="12" size="12">
          Вместо обычных ссылок <br /> вы сможете интегрировать в неограниченное
          количество стримов <br /> уникальный промо-формат
        </Text>

        <List>
          <Joystick src={joystick} alt="Joystick" loading="lazy" />
          <div />

          <ListItem>
            <LazyLoadImage src={ecom_item_3} alt="" loading="lazy" />
            <Text>
              Не всем товарам нужна демонстрация: иногда достаточно промо-кода,
              либо интерактивного видео, встроенного в стрим
            </Text>
            <Arrow src={yellow_arrow} alt="" loading="lazy" />
          </ListItem>

          <ListItem>
            <LazyLoadImage src={ecom_item_1} alt="" loading="lazy" />
            <Text>
              Доставка товаров занимает время + дополнительные издержки на
              логистику, косты могут быть не оправданы
            </Text>
            <Arrow src={violet_arrow} alt="" loading="lazy" />
          </ListItem>

          <ListItem>
            <LazyLoadImage src={ecom_item_2} alt="" loading="lazy" />
            <Text>
              Как следствие: не все товары можно показать физически, <br /> не
              все товары удобно интегрировать в стримы
            </Text>
          </ListItem>

          <Promo></Promo>
        </List>

        <Text fix_size="12" size="12">
          Узнайте, как SocialStream помогает <br /> в продажах товаров
          интернет-магазинов
        </Text>
        <Button>Начать общение</Button>
      </InnerWrap>
    </SECommerce>
  );
};

export default ECommerce;
